import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-flexbox-grid';
import querystring from 'querystring'

class ContactLayout extends Component {
constructor(props) {
  super(props)
  const form = {
    name: '',
    email: '',
    text: ''
  }
  this.state = {
    form,
    messageSend: false,
    showEmailOnly: false 
    }
  }

  handleInpuChange = (ev, field) => {
    const newState = this.state.form
    newState[field] = ev.target.value
    this.setState({form:newState})
  }
  
  sendMessage = (e) => {
    const { name, email, text } = this.state.form
    const body = querystring.stringify(this.state.form)
    console.log(body);
    fetch('/api/sendEmail', {  
      method: 'POST',  
      headers: {  
        "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"  
      },  
      body: body
    })
    .then(res => res.json({}))  
    .then((data) => {
      console.log('Request succeeded with JSON response', data);  
      data.status && data.status == true ? this.setState({ form: { name: '', email: '', text: '' }, messageSend: 'Сообщение успешно отправлено!' }) :
        this.setState({messageSend: 'При отправке сообщения возникла ошибка, попробуйте пожалуйста позже', showEmailOnly: true})
        console.log('Request failed');  
    })  
      .catch((error) => {  
      this.setState({messageSend: 'При отправке сообщения возникла ошибка, попробуйте пожалуйста позже', showEmailOnly: true})
      console.log('Request failed', error);  
    });
  }

  render() {
    return (
      <div ref="contact">
        <Grid className="contactWrapper">
          <Row center="xs" className="contactTitleRow">
            <Col xs={12}>
              {
                !this.state.messageSend ? <p className="contactTitleText">Для связи со мной используйте форму:</p> : <p className="contactTitleText">{this.state.messageSend}</p>
              }  
            </Col>
          </Row>
          {
            !this.state.messageSend &&
              <Row center="xs" className="contactRow">
                <Col className="contactForm">
                  <p className="inputTitle">name:</p>
                  <input className="name" type="text" value={this.state.form.name} onChange={(ev) => this.handleInpuChange(ev, 'name')} />
                  <p className="inputTitle">email:</p>
                  <input className="email" type="email" value={this.state.form.email} onChange={(ev) => this.handleInpuChange(ev, 'email')} />
                  <p className="inputTitle">text:</p>
                  <textArea className="text" type="text" value={this.state.form.text} onChange={(ev) => this.handleInpuChange(ev, 'text')} />
                  <div className="btnWrapper"><button className="btn" onClick={(e) => this.sendMessage(e)}>Отправить</button></div>
                </Col>  
              </Row>
          }
          {
            !this.state.messageSend || this.state.showEmailOnly &&
            <Row center="xs" className="contctOrRow">
              <Col className="contctOrCol">
                <p className="orTitle">или напишите мне</p>
                <p><a href="mailto:sapronovkd@gmail.com" className="orText">sapronovkd@gmail.com</a></p>  
              </Col>  
            </Row>
          }
        </Grid>
      </div>
    )
  }
}

export default ContactLayout


