import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-flexbox-grid';
import {StyleRoot} from 'radium';

import RunningLine from '../components/RunningLine'
import MainImage from '../components/MainImage'

class MainContent extends Component {
  constructor(props) {
    super(props)
    const lines = {
      top: false,
      bottom: false
    }
    const mainImage = {
      show: false,
      animate: false
    }
    this.state = {
      lines,
      showPhoto: false,
      mainImage
    }
  }
  

  componentDidMount() {
    setTimeout(() => {
      this.triggerLine(['top', 'bottom'])
    }, 2000);
    setTimeout(() => {
      const newState = this.state.mainImage
      newState.animate = true
      newState.show = true
      this.refs.home && this.setState({
        mainImage: newState
      })
    }, 200)
  }
  
  componentWillUnmount() {
    this.setState({
      mainImage: {
        show: false,
        animate: false
      }
    })
  }


  triggerLine = (linesArr) => {
    const newState = this.state.lines
    linesArr.forEach((item) => {
      newState[item] = !this.state.lines[item]
    });
    this.setState({lines:newState})
  }

  render() {
    return (
      <div ref="home">
        <Grid className="home width100" >
          <Row center="xs" className="homeRow NoLeftAndRightMargin" >
            <Col xs={12} className="homeCol">
              <Row className="topLineRow " >
                <Col xs={12} className="topLineWrapper NoLeftAndRightPadding">
                  <RunningLine prefix="homeTop" itemAmount={1} delay={10} lineItemeStyle={lineItemeStyle} from="right" shouldStayAfter shouldReverse start={this.state.lines.top}  autoOff={() => this.triggerLine('top')}/>  
                </Col>
              </Row>
              <Row className="homeImage" >
                <StyleRoot className="width100">
                  {
                    this.state.mainImage.show && <MainImage shouldAnimate={this.state.mainImage.animate} showImg={this.state.mainImage.show}/>
                  }
                </StyleRoot>
              </Row>
              <Row className="bottomLineRow" >
                <Col xs={12} className="bottomLineWrapper NoLeftAndRightPadding">
                  <RunningLine prefix="homeBottom" itemAmount={1} delay={10} lineItemeStyle={lineItemeStyle} from="left" shouldStayAfter shouldReverse start={this.state.lines.bottom}  autoOff={() => this.triggerLine('bottom')}/>  
                </Col>
              </Row>
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}

const lineItemeStyle = {
  height: '40%',
  background: '#0074d9',
  width: '1.1988%',
  borderRadius: '8%'
}

export default MainContent


