import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-flexbox-grid';
import { push } from 'react-router-redux';
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { history, store } from '../api/ReduxStore'


import RunningLine from '../components/RunningLine'

class NavLink extends Component {
  constructor(props) {
    super(props)
    this.state = {
      line: false,
      forceQuit: false
    }
  }

  triggerLine = () => {
    this.setState({line: false})
  }

  goToLink = (link) => {
    this.setState({ line: true })
    setTimeout(() => {
      store.dispatch(push(link))
    }, 100);
  }


  render() {
    return (
      <div className="navLink" onClick={() =>  this.goToLink(this.props.to)}>
        <p className={this.props.pathname == this.props.to ? 'navLinkInnerText active' : 'navLinkInnerText'}>{this.props.text}</p>
        <div className="runningLineWrapper">
          <RunningLine prefix="mainContentBottom" itemAmount={1} delay={10} lineItemeStyle={lineItemeStyle} from="left" start={this.state.line}  autoOff={() => this.triggerLine()} />  
        </div>
      </div>
    )
  }
}

const lineItemeStyle = {
  height: '30%',
  background: '#0074d9',
  width: '6.4%',
  borderRadius: '6%'
}

class TopMenu extends Component {
  render() {
    return (
      <Grid fluid className="topMenu">
        <Row center="xs" className="topMenuRow">
          <Col xs={12} sm={6} md={3} className="topMenuCol">
              <NavLink to="/" text="Главная" pathname={this.props.location.pathname} />
          </Col>
          <Col xs={12} sm={6} md={3} className="topMenuCol">
              <NavLink to="/about" text="Обо мне" pathname={this.props.location.pathname} />  
          </Col>
          <Col xs={12} sm={6}  md={3} className="topMenuCol">
              <NavLink to="/projects" text="Проекты" pathname={this.props.location.pathname} />  
          </Col>
        </Row>
         <Row center="xs" className="topMenuRow">
          <Col xs={12} sm={12} md={3} className="topMenuCol">
             <NavLink to="/contats" text="Контакты" pathname={this.props.location.pathname} />  
          </Col>
        </Row>
      </Grid>
    )
  }
}


const mapStateToProps = state => ({
  location: state.routing.location
})

export default connect(mapStateToProps)(TopMenu)


