import React, { Component } from 'react'
import {StyleRoot} from 'radium';
import Radium from 'radium'
import { Grid, Row, Col } from 'react-flexbox-grid';
import { Link } from 'react-router-dom'

@Radium
class Project extends Component {
  constructor(props) {
    super(props)
    this.state = {
      
    }
  }

  render() {
    return (
      <Col xs={3}>
        <div className="projectItem">
          <img className="projectItemImg" src={require(`../../public/img/projectsImg/${this.props.itemData.dynamicFolder}/mainImg.png`)}>
          </img>
          <Link className="projectItemImgInner" to={{pathname: `/projects/${this.props.itemData.dynamicFolder}`,state: { modal: true }}}>
            <p className="projectItemImgInnerText">{this.props.itemData.title}</p>
          </Link>
        </div>
      </Col>
    )
  }
}

export default Project


