import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { Grid, Row, Col } from 'react-flexbox-grid';
import { store } from '../api/ReduxStore'
import { push } from 'react-router-redux';

import projectsArr from '../constants/projects'


class ProjectLayout extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
  }
  
  
  componentWillMount() {
    console.log('projects');
  }
  
  componentDidMount() {
    setTimeout(() => this.setState({showModal: true}),0)
  }
  
  handleClick = (event) => {
    if (event.target.className == 'modalOverlay show') {
      this.closeModal()
    }
  }

  closeModal = () => {
    this.setState({ showModal: false }) 
    setTimeout(() => store.dispatch(push('/projects')), 200)
  }

  render() {
    const projectId = this.props.match.params.id
    const project = projectsArr.find((item) => Number(item.dynamicFolder) == projectId)
    return (
      <div className={this.state.showModal ? 'modalOverlay show' : 'modalOverlay'} onClick={this.handleClick}>
        <div className="modalInner">
          <Grid fluid className="contentWrapper">
            <Row className="contentRow">
              <Col xs={6} className="imageCol">
                <Row className="mainImgRow">
                  <Col xs={12} className="mainImgCol">
                    {
                      project.mainImg && <img className="mainImg" src={require(`../../public/img/projectsImg/${projectId}/mainImg.png`)} />
                    }  
                  </Col>
                </Row>
                <Row className="otherImgRow">
                  <Col xs={4} className="otherImgCol">
                    <div className="imgWrapper">
                      {
                        project.img1 && <img className="smallImg" src={require(`../../public/img/projectsImg/${projectId}/img1.png`)}  />
                      }
                    </div>
                  </Col>
                  <Col xs={4} className="otherImgCol">
                    <div className="imgWrapper">  
                      {
                        project.img2 && <img className="smallImg" src={require(`../../public/img/projectsImg/${projectId}/img2.png`)}  />
                      }
                    </div>
                  </Col>
                  <Col xs={4} className="otherImgCol">
                    <div className="imgWrapper">
                      {
                        project.img3 && <img className="smallImg" src={require(`../../public/img/projectsImg/${projectId}/img3.png`)}  />
                      }
                    </div>
                  </Col>
                </Row> 
              </Col>
              <Col xs={6} className="descriptionCol">
                <Row className="titleWrapperRow">
                  <Col xs={12} className="titleWrapper">
                    <p className="title">{project.title}</p> 
                    <a href={project.link} className="link">{project.link}</a>
                  </Col>  
                </Row>
                <Row className="descriptionWrapperRow">
                  <Col xs={12} className="descriptionWrapper">
                    <p className="description">{project.description}</p> 
                    <p className="stackPre">Стек используемых технологий:</p>
                    <div className="stack">{project.stack.split(' ').map((item, i) => <div className="stackItem" key={'stack_' + i}><p className="stackItemText">{item}</p></div>)}</div>
                  </Col>  
                </Row>
              </Col>
            </Row>
          </Grid>
          <div onClick={this.closeModal} className="closeBtn">
            <p className="closeBtnText">x</p>
          </div>
        </div>
      </div>
    )
  }
}

export default ProjectLayout


