import React, { Component } from 'react'
import { Grid, Row, Col } from 'react-flexbox-grid';

import RunningLine from '../components/RunningLine'

class AboutLayout extends Component {
  render() {
    return (
      <div ref="projecs">
        <Grid className="aboutWrapper">
          <Row className="aboutRow">
            <Col xs={12} className="aboutCol">
              <p className="title">Привет! Меня зовут Кирилл.</p>  
            </Col>
          </Row>
          <Row className="textRow">
            <Col xs={1} />
            <Col xs={10} className="textCol">
              <p className="text">Я живу в Москве. На данный момент я занимаюсь разработкой, созданием и поддержкой cайтов и web приложений. Закончил Российский экономический университет имени Г. В. Плеханова по специальности Прикладная информатика бакалавриат. И немного похвастаюсь с диломом на отлично =). Так же закончил Британский банковско-финансовый колледж по программе BTEC First Certificate/Diploma in Business. Уровень знания английского языка 6 IELTS. Програмированием занимаюсь со старших классов школы. Начинал с python. Далее увлекся программирование микроконтроллеров avr - C/C++. После этого заинтресовался написанием приложений для ios - Objective-c. Далее был перерерыв, только иногда писал код для себя, из-за учебы. Так же пару раз сталкивался со swift, C#, java. После, увлекся web разработкой = php. Достаточно большое время использовал этот язык. После открыл для себя javascript. Сейчас стараюсь заниматься разработкой только на этом языке, потому что он мне очень симпатичен.</p>  
              <p className="text">Мои хобби включают в себя катание на велосипеде, игра в настольный теннис, катание на сноуборде, иногда, берусь снова за микроконтроллеры, так же интересен тюнинг автомобилей. Ну и конечно же моя работа = мое хобби.</p>
              <p className="text">Умения: js, es6/es7, react.js, meteor, many different routers, spacebars, redux, redux-thunk, react-native, express, BEM, gulp, webpack, scss, postprocessor, jscss, adaptive layout, jquery, flexbox, nginx, git, git-cli, bitrix24, mongodb, mysql, graphql, bash, rest api, yarn, npm, bootstrap, semantic-ui. Всего по-немногу и не только: php, Objective-c, C, C#, C++, java, jira, koa, svg. И огромное количество разнообразных npm пакетов.</p>
            </Col>  
          </Row>
        </Grid>
      </div>
    )
  }
}

export default AboutLayout


