import React, { Component } from 'react'
import { Grid, Row, Col, getRowProps, getColumnProps } from 'react-flexbox-grid';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup'


import RunningLine from '../components/RunningLine'
import Project from '../components/Project'

import projectsArr from '../constants/projects'


class ProjectsLayout extends Component {
  constructor(props) {
    super(props)
    const rowCount = Math.floor(projectsArr.length / 4)
    const lines = Array(rowCount).fill().map((item, i) => ({top: false, bottom: false}))
    const projects = projectsArr.reverse().map((item, i) => {
      item.show = false
      return item
    })
    this.state = {
      rowCount,
      lines,
      projects
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.triggerLine(['top', 'bottom'])
      this.rowTimer()
    }, 200);
  }

  triggerLine = () => {
    const newState = this.state.lines
    newState.map((item, i) => {
      item.top = true
      item.bottom = true
    })
    this.setState({lines:newState})
  }

  rowGenerator = (projects) => {
    const rowCount = this.state.rowCount
    return Array(rowCount).fill().map((item, i) => {
      const reverse = i % 2 != 0
      return (
        <div className="width100 rowLineWrapper" key={'row_' + i}>
          <Row className="runningLineTopRow width100">
            <RunningLine prefix="homeBottom" itemAmount={2} delay={2} lineItemeStyle={lineItemeStyle} from={reverse ? 'right' : 'left'} shouldStayAfter shouldReverse start={this.state.lines[i].top}  autoOff={() => this.triggerLine('bottom')}/>  
          </Row>
            <CSSTransitionGroupRow addClass={reverse ? ' reverse' : null}>
              {
                 this.state.projects && this.state.projects.map((item2, index) => {
                    const rowCheck = i * 4 <= index && index < i * 4 + 4
                    if (rowCheck && item2.show) {return <Project itemData={item2} index={index} key={'row_' + i + '_proj_' + item2.dynamicFolder}/>} else null 
                 })
              }
            </CSSTransitionGroupRow>
          <Row className="runningLineTopRow width100">
            <RunningLine prefix="homeBottom" itemAmount={2} delay={22} lineItemeStyle={lineItemeStyle} from={reverse ? 'right' : 'left'} shouldStayAfter shouldReverse start={this.state.lines[i].bottom}  autoOff={() => this.triggerLine('bottom')}/>  
          </Row>
        </div>
      )
    })
  }

  rowTimer = (numb = 0) => {
    const newState = this.state.projects
    const rowCount = this.state.rowCount
    Array(rowCount).fill().map((item, i) => newState[i * 5 + numb] ? newState[i * 5 + numb].show = true : null)
    this.setState({projects:newState})
    numb + 1 < 5 && setTimeout(() => this.rowTimer(numb+1), 300)
  }

  render() {
    return (
      <div ref="projecs">
        <Grid className="projecs width100">
          <Row className="projecsRow NoLeftAndRightMargin">
            <Col xs={12} className="projecsCol">
              {
                this.rowGenerator(this.state.projects)
              }
            </Col>
          </Row>
        </Grid>
      </div>
    )
  }
}

const lineItemeStyle = {
  height: '40%',
  background: '#0074d9',
  width: '1.1988%',
  borderRadius: '8%'
}

const CSSTransitionGroupRow = (props) => {
  const colProps = getColumnProps(props);
  const rowProps = getRowProps(props);
  const className = 'innerProjectsRow width100 NoLeftAndRightMargin ' + colProps.className + ' ' + props.addClass
  return (
    <CSSTransitionGroup
              className={className}
              transitionName="showProjects"
              transitionEnterTimeout={4000}
              transitionLeaveTimeout={4000}>
      {props.children}
    </CSSTransitionGroup>
  );
}

export default ProjectsLayout


