import React, { Component } from 'react';
import { Grid, Row, Col } from 'react-flexbox-grid';
import '../constants/stylesheets/styles.scss'

import TopMenu from '../components/TopMenu'
import Footer from '../components/Footer'
import MainContentLayout from './MainContentLayout'
import ProjectsLayout from './ProjectsLayout'

class MainLayout extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {

  }

  render() {
    return (
      <Grid fluid className="mainLayout NoLeftAndRightPadding">
        <Row center="xs" className="topMenuRow NoLeftAndRightMargin">
          <Col xs={6} className="topMenuWrapper">
            <TopMenu />
          </Col>
        </Row>
        <Row className="mainContentRow">
          <Col xs={12} className="mainContentWrapper">
            {this.props.children}
          </Col>
        </Row>
        <div className="footerRowWrapper">
          <Row center="xs" className="footerRow NoLeftAndRightMargin">
            <Col xs={6} className="footerWrapper">
              <Footer />
            </Col>
          </Row>
        </div>
      </Grid>
    )
  }
}

export default MainLayout


