import React, { Component } from 'react'
import mainPhoto from '../../public/img/mainPhoto.jpg'
import { bounce } from 'react-animations'
import Radium from 'radium';

@Radium
class MainImage extends Component {
  render() {
    return (
      <div className="mainImageWrapper width100">
        <img style={[this.props.shouldAnimate ? styles.bounce : null]} className="mainImg" src={mainPhoto}/>
      </div>
    )
  }
}


const styles = {
  bounce: {
    animation: 'x 2s',
    animationName: Radium.keyframes(bounce, 'bounce'),
  }
}

export default MainImage


