import React, { Component } from 'react'
import ReactDOM from 'react-dom';
import Radium from 'radium'
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup'

@Radium
class LineItem extends Component {
  render() {
    const { lineItemeStyle, index, isLast} = this.props
    return (
      <div style={{padding: '3px'}}>
        <div style={lineItemeStyle} />
      </div>
    )
  }
}

class RunningLine extends Component {
  constructor(props) {
    super(props)
    this.state = {
      lineItems: [],
      count: 0,
    }
  }

  componentWillMount = () => {

  }

  componentDidMount = () => {
    const parentWidth = ReactDOM.findDOMNode(this.refs.runningLine).parentNode.getBoundingClientRect().width
    const parentHeight = ReactDOM.findDOMNode(this.refs.runningLine).parentNode.getBoundingClientRect().height
    const parentDementions = {width: parentWidth, height: parentHeight}
    this.setState({parentDementions})
    if (this.props.start && this.state.curState != 'inProcess') {
      this.setState({curState: 'inProcess'})
      this.generator('increment')
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.start != nextProps.start && nextProps.start) {
      if (this.state.curState != 'inProcess') {
        this.setState({curState: 'inProcess', todo: 'increment'})
        this.generator('increment')
      }
    }
    if (this.props.start != nextProps.start && !nextProps.start) {
      if (this.state.curState == 'finished') {
        this.setState({curState: 'inProcess', todo: 'decriment'})
        this.generator('decriment')
      }
    }
  }

  generator = (todo) => {
    if (this.refs.runningLine) {
      const itemWidthPerc = Number(this.props.lineItemeStyle.width.replace('%', ''))
      const itemWidthCalc = this.state.parentDementions.width / 100 * itemWidthPerc
      const allItemsWidth = (itemWidthCalc + 6) * this.state.count
      const increment = todo == 'increment'
      const decriment = todo == 'decriment'
      if ((this.state.parentDementions.width > allItemsWidth && increment|| decriment && this.state.count > 0)) {
        let todoCulc = increment ? this.state.count + this.props.itemAmount : this.state.count - this.props.itemAmount
        setTimeout(() => {
          this.setState({count: todoCulc})
          this.generator(todo)
        }, this.props.delay)
      } else {
        this.setState({curState: 'finished', forceQuit: false})
        if (todo == 'increment' && !this.props.shouldStayAfter) {
          this.props.autoOff()
        }
      }
    }
  }

  render() {
    const { prefix, lineItemeStyle, start, from } = this.props
    let className = from
    if (this.state.todo == 'decriment' && this.props.shouldReverse) {
      if (from == 'right') {
        className = ' left'
      } else if (from == 'left') {
        className = ' reverse'
      }
    }
    const itemComputeWidth = this.state.parentDementions ? this.state.parentDementions.width / 100 * Number(lineItemeStyle.width.replace('%', '')) : ''
    const itemComputeHeight = this.state.parentDementions ? this.state.parentDementions.height / 100 * Number(lineItemeStyle.height.replace('%', '')) : ''
    const itemComputeBorderRadius = this.state.parentDementions ? this.state.parentDementions.width / 100 * Number(lineItemeStyle.borderRadius.replace('%', '')) : ''
    const lineItemeStyleMod = {...lineItemeStyle, width: itemComputeWidth, height: itemComputeHeight, borderRadius: itemComputeBorderRadius}
    return (
      <div ref="runningLine" className="runningLineWrapper">
        <CSSTransitionGroup
          className={className}
          transitionName="runningLine"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}>
          {Array(this.state.count).fill().map((item, i) => <LineItem key={prefix + '_' + i} lineItemeStyle={ lineItemeStyleMod } allCount={this.state.count}/>)}
        </CSSTransitionGroup>
      </div>
    )
  }
}

export default RunningLine


