import React, { Component } from 'react'
import { Provider } from 'react-redux'
import { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom'
import { ConnectedRouter } from 'react-router-redux'

import { store, history } from './api/ReduxStore'

import MainLayout from './layouts/MainLayout'
import MainContentLayout from './layouts/MainContentLayout'
import ProjectsLayout from './layouts/ProjectsLayout'
import ProjectLayout from './layouts/ProjectLayout'
import AboutLayout from './layouts/AboutLayout'
import ContactLayout from './layouts/ContactLayout'
import ErrorLayoit from './layouts/ErrorLayout'

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <ConnectedRouter history={history}>
          <div>
            <MainLayout>
              <Switch>
                <Route exact path="/" component={MainContentLayout}/>
                <Route path="/projects" component={ProjectsLayout}/>
                <Route path='/about' component={AboutLayout}/>
                <Route path='/contats' component={ContactLayout}/>
                <Route component={ErrorLayoit}/>
              </Switch>
              <Route path='/projects/:id' component={ProjectLayout}/>
            </MainLayout>
          </div>
        </ConnectedRouter>
      </Provider>
    )
  }
}

export default App



